:root {
  /*primary colors*/
  --clr-primary: hsl(192, 37%, 48%);
  --clr-secondary: hsl(268, 34%, 53%);
  /* neutrals */
  --clr-neutral-800: hsl(240, 21%, 20%);
  --clr-neutral-400: hsl(240, 10%, 57%);
  --clr-neutral-300: hsl(192, 90%, 77%);
  --clr-neutral-200: hsl(278, 100%, 86%);
  --clr-neutral-100: hsl(0, 0%, 98%);
  --fs-300: 18px; /*l-h 26px*/
  --fs-600: 64px;
  --fs-500: 40px;
  --fs-400: 1rem;
  --fw-400: 400;
  --fw-600: 600;
  --fw-700: 700;
}

html {
  font-family: "Red Hat Display";
}

body {
  font-size: var(--fs-300);
  line-height: 26px;
  font-weight: var(--fw-400);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  padding: 48px 0;
  text-align: center;
}

.logo {
  color: var(--clr-neutral-800);
  font-weight: 900; /* var(--fw-700); */
  text-decoration: none;
  font-size: 32px;
}

.counter {
  position: absolute;
  top: 0;
  font-weight: bold;
  font-size: 1rem;
  line-height: 26px;
  position: absolute;
  left: 50%;
  translate: -50%;
  padding: 0.9rem 1.2rem;
  background-color: #fff;
  border: 1px solid #d8d8d8;
  color: var(--clr-neutral-400);
  border-radius: 50%;
  top: calc((13px + 0.9rem) * -1);
}

.counter::before {
  content: "";
  background-color: #d8d8d8;
  width: 1px;
  height: 84px;
  top: -84px;
  left: 50%;
  translate: -50%;
  position: absolute;
}

h1 {
  font-size: var(--fs-500);
  line-height: 2.75rem;
  font-weight: 900;
  max-inline-size: 10ch;
  margin: 0 auto;
  color: var(--clr-neutral-800);
}
@media (min-width: 768px) {
  h1 {
    font-size: 3rem;
    line-height: 1;
  }
}
@media (min-width: 1280px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  /*font-size: var(--fs-500);
  line-height: 2.75rem;
  font-weight: var(--fw-600);*/
  font-size: 32px;
  line-height: 36px;
  font-weight: 900;
  text-align: center;
  color: var(--clr-neutral-800);
}
@media (min-width: 1280px) {
  h2 {
    font-size: 2.5rem;
    line-height: 2.75rem;
    max-inline-size: 16ch;
    margin: 0 auto;
  }
}

p {
  color: var(--clr-neutral-400);
  font-size: 1rem;
  letter-spacing: 0.2px;
  padding: 1.6rem 0;
}
@media (min-width: 1280px) {
  p {
    letter-spacing: 0.2px;
    font-size: 1.125rem;
    line-height: 1.625rem;
    max-inline-size: 44ch;
    margin: 0 auto;
  }
}

.overline {
  font-size: var(--fs-400);
  font-weight: var(--fw-400);
  color: var(--clr-primary);
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 4px;
  padding-top: 4rem;
  padding-bottom: 1.5rem;
}
@media (min-width: 1280px) {
  .overline {
    padding-top: 5.2rem;
  }
}

main,
.header_wrapper,
footer {
  max-width: 1440px;
  margin: 0 auto;
}

footer {
  padding: 80px 1.5rem;
  margin-top: 40px; /**/
}
@media (min-width: 1280px) {
  footer {
    margin-top: 80px;
    padding: 80px 80px;
  }
}

header {
  /*  background-color: blue; */
  padding-bottom: 80px;
}

main {
  margin-top: 110px;
  padding-top: 64px;
  padding: 80px 1.5rem;
  /* background-color: red; */
}
@media (min-width: 1280px) {
  main {
    margin-top: 150px;
  }
}

/* buttons */
.btn {
  border-radius: 1.8125rem;
  color: var(--clr-neutral-100);
  border: 0;
  padding: 1.25rem 2.5rem;
  font-size: var(--fs-400);
  font-weight: var(--fw-600);
}
.btn:hover {
  cursor: pointer;
}

.btn_primary {
  background-color: var(--clr-primary);
}

.btn_primary span {
  color: var(--clr-neutral-300);
}

.btn_primary:hover {
  background-color: #71c0d4;
}

.btn_secondary {
  padding: 1.25rem 1.9rem;
  background-color: var(--clr-secondary);
}

.btn_secondary span {
  color: var(--clr-neutral-200);
}

.btn_secondary:hover {
  background-color: #b18bdd;
}

header {
  background-image: url("../images/tablet/image-hero.png");
  background-size: 110%;
  background-repeat: no-repeat;
  background-position: top center;
  max-width: 1500px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  header {
    background-size: auto;
  }
}
@media (min-width: 1280px) {
  header {
    background: url("../images/desktop/image-hero-left.png"), url("../images/desktop/image-hero-right.png");
    background-size: auto, auto;
    background-position: 0px 0, top 60px right;
    background-repeat: no-repeat, no-repeat;
    padding-top: 3.5rem;
  }
}
header .header__content {
  padding-top: 50%;
  text-align: center;
}
@media (min-width: 768px) {
  header .header__content {
    padding-top: 400px;
  }
}
@media (min-width: 1280px) {
  header .header__content {
    padding-top: 0px;
  }
}
header .header__content p {
  padding: 1.5rem;
}
@media (min-width: 1280px) {
  header .header__content p {
    max-inline-size: 49ch;
    margin: 0 auto;
  }
}
header .header__actions {
  padding-top: 0.55rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  header .header__actions {
    flex-direction: row;
    justify-content: center;
  }
}

main {
  position: relative;
  text-align: center;
}

main .gallery {
  display: grid;
  grid-template-columns: 151px 151px;
  justify-content: center;
  gap: 25px;
  max-width: 1110px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  main .gallery {
    grid-template-columns: repeat(4, 164px);
  }
}
@media (min-width: 1280px) {
  main .gallery {
    justify-content: center;
    grid-template-columns: repeat(4, 255px);
    margin-top: 1rem;
  }
}

.gallery img {
  width: 151px;
  height: 143px;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .gallery img {
    width: 164px;
    height: 156px;
  }
}
@media (min-width: 1280px) {
  .gallery img {
    width: 255px;
    height: auto;
  }
}

main p {
  /*  padding: 2rem 0;
  font-size: 1rem;
  font-weight: 500;
  line-height: 26px; */
}

footer {
  --opacity: 0.8;
  background: linear-gradient(hsla(192, 37%, 48%, var(--opacity)), hsla(192, 37%, 48%, var(--opacity))), url("../images/mobile/image-footer.jpg");
  position: relative;
  background-size: auto, cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  footer {
    background: linear-gradient(hsla(192, 37%, 48%, var(--opacity)), hsla(192, 37%, 48%, var(--opacity))), url("../images/tablet/image-footer.jpg");
    background-size: auto, cover;
  }
}
@media (min-width: 1280px) {
  footer {
    background: linear-gradient(hsla(192, 37%, 48%, var(--opacity)), hsla(192, 37%, 48%, var(--opacity))), url("../images/desktop/image-footer.jpg");
    background-size: auto, cover;
    background-repeat: repeat, no-repeat;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: left;
    justify-content: space-evenly;
    justify-items: center;
  }
}

footer p {
  padding: 2rem 0;
}

footer h2,
footer p {
  color: var(--clr-neutral-100);
}

.flex-column {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.flex-between {
  justify-content: space-between;
}

.p-2 {
  padding: 2rem;
}